import React from "react";
import { ColumnCenter } from "../styled";
import { SubTitle } from "./styled";
import { Document, Page, pdfjs } from "react-pdf";
// import { IMapInfo } from "./action";
pdfjs.GlobalWorkerOptions.workerSrc = `//cdnjs.cloudflare.com/ajax/libs/pdf.js/${pdfjs.version}/pdf.worker.min.js`;

export const PDFItem = ({ url, title }: { url: string; title: string }) => {
  return (
    <ColumnCenter>
      {url ? (
        <div>
          <div
            style={{
              width: 1400,
              display: "flex",
              justifyContent: "space-between",
            }}
          >
            <SubTitle
              style={{
                marginTop: 0,
                color: "#333",
                marginBottom: 0,
                textAlign: "left",
                position: "relative",
                zIndex: 100,
                top: "60px",
              }}
            >
              {title}
            </SubTitle>
          </div>
          <Document file={url}>
            <Page width={1400} pageNumber={1} />
          </Document>
        </div>
      ) : (
        <SubTitle
          style={{
            marginTop: 0,
            color: "#333",
            marginBottom: 0,
            textAlign: "left",
           
           
          }}
        >
         暂无数据
        </SubTitle>
      )}
    </ColumnCenter>
  );
};

